*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

img {
  width: 40px;
  height: 40px;
}

a, p {
  margin: 0;
  padding: 0;
}

.container {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.container > a {
  align-items: center;
  display: flex;
}

.nav__list {
  text-align: center;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  margin: 0 0 0 20px;
  line-height: 60px;
  text-decoration: none;
}

.nav__link:first-child {
  margin-left: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  transition: all .3s ease-in-out;
}

.card__title {
  width: 166px;
  height: 36px;
  margin-top: 36px;
  margin-left: 16px;
}

.card__product-name {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product {
  margin-top: 4px;
}

.card__product-code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  margin-top: 18px;
  padding-left: 2px;
  display: flex;
}

.card__review-text {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 22px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buttom {
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  line-height: 38px;
}

.card__buttom a {
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
}

.card__buttom:hover {
  background-color: #fff;
}

.card__buttom:hover a {
  color: #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title:hover {
  color: #34568b;
}

.card > img {
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-left: 19px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.grid {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .grid {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .grid {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .grid {
    grid-template-columns: repeat(4, 200px);
  }
}
